.footer-container {
    height: 201px;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    align-items: center;
    max-width: 1440px;
    margin: 0 auto 1px auto;
    
}

.footer-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    margin: 0 auto 0 auto;
}

.footer-text-link {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position:absolute;
}

.footer-text {
    font-family: 'Libre';
    font-size: 35px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.73;
    letter-spacing: -1.09px;
    text-align: center;
    color: white;
    margin: 0 0 0 0;
}

.footer-text-talk-link {
    font-family: 'Lato';
    font-size: 50px;
    font-weight: 900;
    letter-spacing: -1.45px;
    color: #67182e;
    margin: 0 0 0 0;
    text-decoration:underline;
    
}
.footer-text-talk-link:hover {
    color: black;
}

.footer-text-talk-link:hover {
    cursor:pointer;
}

.footer-tb {
    width: 100%;
    margin-top: 0 auto 0 auto;
    height: auto;
}

.footer-divider{
    height:1px;
    background-color: black;
    max-width: 1600px;
    width: 98%;
    margin: 0 auto 0 auto;
}

.footer-bottom-container {
    display: flex;
    justify-content: space-between;
    margin: 0 auto 0 auto;
    width: 98%;
    height: 50px;
    align-items: center;
    max-width: 1600px;
}

.footer-linkedin {
    font-family: 'Lato';
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 2px;
    color: #111;
    text-decoration: none;
}

.footer-linkedin:hover {
    color: #67182e;
    transition: .5s;
}

.footer-github {
    font-family: 'Lato';
    font-size: 16px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 2px;
    text-align: right;
    color: #111;
    text-decoration: none;
}

.footer-github:hover {
    color: #67182e;
    transition: .5s;
}


@media(min-width: 450px) {
    .footer-container {
        margin-top: 20px
    }
}

@media(min-width: 550px) {
    .footer-divider {
        margin-top: 50px;
    }
}

@media(min-width: 650px) {
    .footer-container {
        margin-top: 30px
    }

    .footer-text {
        font-size: 40px;
    }
    
    .footer-text-talk-link {
        font-size: 67px;
    }

    .footer-divider {
        margin-top: 50px;
    }
}

@media(min-width:750px) {
    .footer-text {
        font-size: 50px;
    }

    .footer-text-talk-link {
        font-size: 80px;
    }

    .footer-divider {
        margin-top: 100px;
    }
}


@media (min-width: 1150px) {
    .footer-divider {
        margin-top: 150px;
    }

    .footer-text {
        font-size: 70px;
    }

    .footer-text-talk-link {
        font-size: 85px;
    }
}

@media (min-width: 1350px) {
    .footer-divider {
        margin-top: 150px;
    }

    .footer-text {
        font-size: 80px;
    }

    .footer-text-talk-link {
        font-size: 95px;
    }
}